export class Address {
    address1: string;
    address2: string;
    landmark: string;
    city: string;
    state: string;
    country: string;
    postCode: string;

    clear() {
        this.city = '';
        this.state = '';
        this.postCode = '';
        this.address1 = '';
        this.address2 = '';
        this.landmark = '';
        this.country = '';
    }
}
