// Angular
import {Component, OnInit, ViewChild} from '@angular/core';
import {SelectionModel} from '@angular/cdk/collections';
import {MatPaginator, MatSort} from '@angular/material';
// RXJS
import {tap} from 'rxjs/operators';
import {merge} from 'rxjs';
// Crud
import {QueryParamsModel} from '../../../../../../core/_base/crud';
// Layout
import {OrdersModel} from '../../../../../../core/e-commerce/_models/orders.model';
import {OrdersService} from '../../../../../../core/e-commerce/_services/orders.service';
import {OrdersDatasource} from '../../../../../../core/e-commerce/_data-sources/orders.datasource';

@Component({
    selector: 'kt-orders-table',
    templateUrl: './orders-table.component.html',
    styleUrls: ['./orders-table.component.scss']
})


export class OrdersTableComponent implements OnInit {
    // Public properties
    dataSource: OrdersDatasource;
    displayedColumns = ['orderDate', 'orderID', 'merchantRefNo', 'customerName', 'trackingNo',
        'shippingTypes', 'courierName', 'orderStatus', 'actions'];
    @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
    @ViewChild(MatSort, {static: true}) sort: MatSort;
    selection = new SelectionModel<OrdersModel>(true, []);
    /**
     * Component constructor
     *
     * @param dataTableService: OrdersService
     */
    constructor(private dataTableService: OrdersService) {
    }

    /**
     * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
     */

    /**
     * On init
     */
    ngOnInit() {
        // If the user changes the sort order, reset back to the first page.
        this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));

        /* Data load will be triggered in two cases:
        - when a pagination event occurs => this.paginator.page
        - when a sort event occurs => this.sort.sortChange
        **/
        merge(this.sort.sortChange, this.paginator.page)
            .pipe(
                tap(() => {
                    this.loadItems();
                })
            )
            .subscribe();

        // Init DataSource
        this.dataSource = new OrdersDatasource(this.dataTableService);
        // First load
        this.loadItems(true);
    }

    /**
     * Load items
     *
     * @param firstLoad: boolean
     */
    loadItems(firstLoad: boolean = false) {
        const queryParams = new QueryParamsModel(
            {},
            this.sort.direction,
            this.sort.active,
            this.paginator.pageIndex,
            firstLoad ? 5 : this.paginator.pageSize
        );
        this.dataSource.loadItems(queryParams);
        this.selection.clear();
    }

    /* UI */

    /**
     * Returns item status
     *
     * @param status: number
     */
    getItemStatusString(status: number = 0): string {
        switch (status) {
            case 0:
                return 'Not Delivered';
            case 1:
                return 'Delivered';
        }
        return '';
    }

    /**
     * Returns item CSS Class Name by status
     *
     * @param status: number
     */
    getItemCssClassByStatus(status: number = 0): string {
        switch (status) {
            case 0:
                return 'success';
            case 1:
                return 'info';
        }
        return '';
    }

    /**
     * Returns item condition
     *
     * @param condition: number
     */
    getItemConditionString(condition: number = 0): string {
        switch (condition) {
            case 0:
                return 'New';
            case 1:
                return 'Used';
        }
        return '';
    }

    /**
     * Returns CSS Class name by condition
     *
     * @param condition: number
     */
    getItemCssClassByCondition(condition: number = 0): string {
        switch (condition) {
            case 0:
                return 'success';
            case 1:
                return 'info';
        }
        return '';
    }

    getShippingType(shipping: string = 'std_shipping'): string {
        if (shipping === 'std_shipping') {
            return 'Standard Shipping';
        }
        return 'Fast Shipping';
    }

}
