// Angular
import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { RouterModule, Routes } from "@angular/router";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { HTTP_INTERCEPTORS, HttpClientModule } from "@angular/common/http";
// Fake API Angular-in-memory
import { HttpClientInMemoryWebApiModule } from "angular-in-memory-web-api";
// Translate Module
import { TranslateModule } from "@ngx-translate/core";
// NGRX
import { StoreModule } from "@ngrx/store";
import { EffectsModule } from "@ngrx/effects";
// UI
import { PartialsModule } from "../../../partials/partials.module";
// Core
import { FakeApiService } from "../../../../core/_base/layout";
// Auth
import { ModuleGuard } from "../../../../core/auth";
// Core => Services
import {
	CustomerEffects,
	customersReducer,
	CustomersService,
	ProductEffects,
	ProductRemarkEffects,
	productRemarksReducer,
	ProductRemarksService,
	ProductSpecificationEffects,
	productSpecificationsReducer,
	ProductSpecificationsService,
	productsReducer,
	ProductsService,
} from "../../../../core/e-commerce";
// Core => Utils
import {
	HttpUtilsService,
	InterceptService,
	LayoutUtilsService,
	TypesUtilsService,
} from "../../../../core/_base/crud";
// Shared
import {
	ActionNotificationComponent,
	DeleteEntityDialogComponent,
	FetchEntityDialogComponent,
	UpdateStatusDialogComponent,
} from "../../../partials/content/crud";
// Components
import { ECommerceComponent } from "./e-commerce.component";
// Customers
import { CustomersListComponent } from "./customers/customers-list/customers-list.component";
import { CustomerEditDialogComponent } from "./customers/customer-edit/customer-edit.dialog.component";
// Products
import { ProductAddComponent } from "./products/product-add/product-add.component";
import { ProductsListComponent } from "./products/products-list/products-list.component";
import { ProductEditComponent } from "./products/product-edit/product-edit.component";
import { RemarksListComponent } from "./products/_subs/remarks/remarks-list/remarks-list.component";
import { SpecificationsListComponent } from "./products/_subs/specifications/specifications-list/specifications-list.component";
import { SpecificationEditDialogComponent } from "./products/_subs/specifications/specification-edit/specification-edit-dialog.component";
// Orders
import { OrdersListComponent } from "./orders/orders-list/orders-list.component";
import { OrderEditComponent } from "./orders/order-edit/order-edit.component";
// Material
import {
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatAutocompleteModule,
	MatButtonModule,
	MatCardModule,
	MatCheckboxModule,
	MatDatepickerModule,
	MatDialogModule,
	MatIconModule,
	MatInputModule,
	MatMenuModule,
	MatNativeDateModule,
	MatPaginatorModule,
	MatProgressBarModule,
	MatProgressSpinnerModule,
	MatRadioModule,
	MatSelectModule,
	MatSnackBarModule,
	MatSortModule,
	MatTableModule,
	MatTabsModule,
	MatTooltipModule,
} from "@angular/material";
import { environment } from "../../../../../environments/environment";
import { NgbProgressbarModule } from "@ng-bootstrap/ng-bootstrap";
import { NgxPermissionsModule } from "ngx-permissions";
import { StockListComponent } from "./orders/stock-list/stock-list.component";
import { ManageReturnsComponent } from "./orders/manage-returns/manage-returns.component";
import { ProductsManageComponent } from "./products/products-manage/products-manage.component";
import { PerfectScrollbarModule } from "ngx-perfect-scrollbar";
import { UiSwitchModule } from "ngx-ui-switch";
import { BillingComponent } from "./billing/billing.component";
import { ProfileComponent } from "./profile/profile.component";
import { StoreOrderComponent } from "./orders/store-order/store-order.component";
import { MockupComponent } from "./mockup/mockup.component";
import { SupportComponent } from "./support/support.component";

import { ProductViewComponent } from "./product-view/product-view.component";
import { DesignLibraryComponent } from "./design-library/design-library.component";
import { ThemeModule } from "../../../theme/theme.module";
import { BuilderComponent } from "../../../theme/content/builder/builder.component";
import { BuilderModule } from "../../../theme/content/builder/builder.module";

// tslint:disable-next-line:class-name
const routes: Routes = [
	{
		path: "",
		component: ECommerceComponent,
		// canActivate: [ModuleGuard],
		// data: { moduleName: 'ecommerce' },
		children: [
			{
				path: "",
				redirectTo: "customers",
				pathMatch: "full",
			},
			{
				path: "customers",
				component: CustomersListComponent,
			},
			{
				path: "library",
				component: DesignLibraryComponent,
			},
			{
				path: "orders",
				component: OrdersListComponent,
			},
			{
				path: "orders/stocks",
				component: StockListComponent,
			},
			{
				path: "products",
				component: ProductsListComponent,
			},
			{
				path: "orders/add",
				component: ProductEditComponent,
			},
			{
				path: "orders/view",
				component: ProductViewComponent,
			},
			{
				path: "orders/store",
				component: StoreOrderComponent,
			},
			{
				path: "orders/mockup",
				component: MockupComponent,
			},
			{
				path: "products/edit",
				component: ProductEditComponent,
			},
			{
				path: "products/edit/:id",
				component: ProductEditComponent,
			},
			{
				path: "orders/returns",
				component: ManageReturnsComponent,
			},
			{
				path: "orders/:orderID",
				component: ProductViewComponent,
			},
			{
				path: "products/add",
				component: ProductAddComponent,
			},
			{
				path: "products/manage",
				component: ProductsManageComponent,
			},
			{
				path: "billing",
				component: BillingComponent,
			},
			{
				path: "profile",
				component: ProfileComponent,
			},
			{
				path: "support",
				component: SupportComponent,
			},
		],
	},
];

@NgModule({
	imports: [
		MatDialogModule,
		CommonModule,
		HttpClientModule,
		PartialsModule,
		NgxPermissionsModule.forChild(),
		RouterModule.forChild(routes),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
		MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatTabsModule,
		MatTooltipModule,
		NgbProgressbarModule,
		environment.isMockEnabled
			? HttpClientInMemoryWebApiModule.forFeature(FakeApiService, {
					passThruUnknownUrl: true,
					dataEncapsulation: false,
			  })
			: [],
		StoreModule.forFeature("products", productsReducer),
		EffectsModule.forFeature([ProductEffects]),
		StoreModule.forFeature("customers", customersReducer),
		EffectsModule.forFeature([CustomerEffects]),
		StoreModule.forFeature("productRemarks", productRemarksReducer),
		EffectsModule.forFeature([ProductRemarkEffects]),
		StoreModule.forFeature(
			"productSpecifications",
			productSpecificationsReducer
		),
		EffectsModule.forFeature([ProductSpecificationEffects]),
		PerfectScrollbarModule,
		UiSwitchModule,
		BuilderModule,
	],
	providers: [
		ModuleGuard,
		InterceptService,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: InterceptService,
			multi: true,
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: "kt-mat-dialog-container__wrapper",
				height: "auto",
				width: "900px",
			},
		},
		TypesUtilsService,
		LayoutUtilsService,
		HttpUtilsService,
		CustomersService,
		ProductRemarksService,
		ProductSpecificationsService,
		ProductsService,
		TypesUtilsService,
		LayoutUtilsService,
	],
	entryComponents: [
		ActionNotificationComponent,
		CustomerEditDialogComponent,
		DeleteEntityDialogComponent,
		FetchEntityDialogComponent,
		UpdateStatusDialogComponent,
		SpecificationEditDialogComponent,
		BuilderComponent,
	],
	declarations: [
		ECommerceComponent,
		// Customers
		CustomersListComponent,
		CustomerEditDialogComponent,
		// Orders
		OrdersListComponent,
		OrderEditComponent,
		// Products
		ProductsListComponent,
		ProductEditComponent,
		RemarksListComponent,
		SpecificationsListComponent,
		SpecificationEditDialogComponent,
		StockListComponent,
		ManageReturnsComponent,
		ProductAddComponent,
		ProductsManageComponent,
		BillingComponent,
		ProfileComponent,
		StoreOrderComponent,
		MockupComponent,
		SupportComponent,
		ProductViewComponent,
		DesignLibraryComponent,
	],
})
export class ECommerceModule {}
