import {Component, OnInit} from '@angular/core';

@Component({
	selector: 'kt-product-add',
	templateUrl: './product-add.component.html',
	styleUrls: ['./product-add.component.scss']
})
export class ProductAddComponent implements OnInit {
	product = {
		item: undefined,
		size: undefined,
		color: undefined,
		category: undefined,
		price: undefined,
		sku: undefined
	};
	showItem = false;
	showProduct = false;
	showMode = false;
	showPincode = false;
	categories: string[] = [
		'Men\'s Clothing',
		'Women\'s Clothing',
		'Kid\'s Clothing',
		'Sublimation Phone Cases',
		'Pop Gris',
	];
	Items: string[] = [
		'Full Sleeve Round Neck T-shirt',
		'Half-sleeve Round Neck T-shirt',
	];
	Size: string[] = ['L', 'S', 'M', 'XL', '2XL'];
	Varieties: string[] = ['White', 'Black', 'Purple'];

	constructor() {
	}

	ngOnInit() {
	}

	categorySelection(value, product) {
		this.showProduct = true;
		product.category = value;
	}

	ItemSelection(value, product) {
		this.showItem = true;
		product.item = value;
	}

	colorSelection(value, product) {
		this.showItem = true;
		product.color = value;
	}

	sizeSelection(value, product) {
		this.showItem = true;
		product.price = 100.00;
		product.size = value;
	}

	modeSelection(value, product) {
		this.showMode = true;
		product.mode = value;
	}
}
