import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'kt-mockup',
  templateUrl: './mockup.component.html',
  styleUrls: ['./mockup.component.scss']
})
export class MockupComponent implements OnInit {
  showProduct = false;
  category = undefined;
  items = undefined;
  colorChoice = [];
  styleChoice = undefined;
  
  categories: string[] = [
		"Men's Clothing",
		"Women's Clothing",
		"Kid's Clothing",
		"Sublimation Phone Cases",
		"Pop Gris",
  ];
  teams: any[] = [
    { name: 'Mens Clothing' },
    { name: 'Womens Clothing' },
    { name: 'Kids Clothing' },
    { name: 'Sublime Phone Cases' },
    { name: 'Pop Gris' },
];
  colors: string[] = [
    'blue',
    'red',
    'yellow',
    'green',
    'white'
  ];
  styles = [1, 2, 3, 4]

  constructor() { }

  ngOnInit() {
  }
  categorySelection(value, product) {
		this.showProduct = true;
		product.category = value;
	}

  selectAllColors(){
    this.resetColors();
    for(let color of this.colors){
      this.colorChoice.push(color);
    }
  }

  resetColors(){
    console.log(this.colorChoice);
    for(let i=0 ; i<this.colorChoice.length ; ++i){
      this.colorChoice.pop();
    }
  }

  addColor(color){
    this.colorChoice.push(color);
  }

}
